import React, { useState } from 'react'
import {InputLabel, Select, MenuItem, FormControl, Button, TextField, Switch, FormGroup, FormControlLabel } from '@material-ui/core'
import { useDispatch, useSelector } from "react-redux";
import {Outlet, Link, useParams} from 'react-router-dom'
import { filterList, eventAdded } from '../Components/List/listSlice';
import { nanoid } from '@reduxjs/toolkit'
import useStyles from '../Components/Filters/styles'
import { parksData } from '../Data/data';

const types = ['running', 'spikeball', 'volleyball', 'basketball', 'yoga', 'hobby', 'soccer']

export default function AddEventForm(props)  {
  let params = useParams()
  const park = useSelector(state => state.list.filteredList.find(park => park.id === params.id))
  const [name, setName] = useState('')
  const [numPeople, setNumPeople] = useState('')
  const [type, setType] = useState('')
  const [info, setInfo] = useState('')
  const [hiddenEvent, sethiddenEvent] = useState(false)
  const dispatch = useDispatch()
  const classes = useStyles()

  const onNameChanged = e => setName(e.target.value)
  const onNumPeopleChanged = e => setNumPeople(e.target.value)
  const onTypeChanged = e => setType(e.target.value)
  const onInfoChanged = e => setInfo(e.target.value)
  const onhiddenEventChange = (e) => sethiddenEvent(e.target.value)

  const onSaveEventClicked = () => {
    if (name && type && info) {
      const newEvent = {
        id: nanoid(),
        name: name,
        location: park.id,
        numPeople: numPeople,
        type: 'event',
        sport: type,
        info: info,
        private: hiddenEvent
      }
      dispatch(eventAdded(newEvent))
      setName('')
      setNumPeople('')
      setType('')
      setInfo('')
      sethiddenEvent(false)
    }
  }

  return (
    <div>
      <h2>Create event at {park.name}</h2>
        <FormGroup className={classes.formControl}>
            <TextField type="text" label="Name" id="eventName" name="eventName" value={name} onChange={onNameChanged}/>
            <InputLabel id="numPeople">Current group size</InputLabel>
            <Select label="People" id="num-people" value={numPeople} onChange={onNumPeopleChanged}>
              <MenuItem value='1'>1</MenuItem>
              <MenuItem value='2+'>2+</MenuItem>
              <MenuItem value='5+'>5+</MenuItem>
              <MenuItem value='10+'>10+</MenuItem>
            </Select>
            <TextField id="eventInfo" label="Info" name="eventInfo" value={info} onChange={onInfoChanged}/>
            <Select value={type} label="Type" onChange={onTypeChanged}>
              {types.map((choice) => (
                  <MenuItem key={nanoid()} value={choice}>{choice}</MenuItem>
              ))}
            </Select>
            <FormGroup>
              <FormControlLabel control={<Switch />} label="Make hidden event" value={hiddenEvent} onChange={onhiddenEventChange} />
            </FormGroup>
            <Button type="button" onClick={onSaveEventClicked}>Save Event</Button>
            <Link to='/'>Cancel</Link>
        </FormGroup>
        <Outlet/>
    </div>
  )
}